import React, { Component } from "react";
import { Modal, Button } from 'antd';

class GroupUserTree extends Component {
    constructor(props){
        super(props);
        this.state = {
            visible: this.props.visible?true:false,
            noModal: this.props.noModal?true:false            
        }
    }
    componentWillReceiveProps(nextProps){
        console.log(nextProps)
        
        this.setState({
            visible: nextProps.visible
        });
    }
    showModal(){
        this.setState({
            visible: true
        });
    }
    handleOk = (e) => {
        console.log(e);
        this.setState({
            visible: false,
        });
    }
    handleCancel = (e) => {
        console.log(e);
        this.setState({
            visible: false,
        });
    }
    render() {
        if(!this.state.noModal){
            return (
                <div>
                    <Modal
                        title="Basic Modal"
                        visible={this.state.visible}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        maskClosable={false}
                        width={"1000px"}
                    >
                        <Tree />
                    </Modal>
                </div>
            );
        }else{
            return (<div><Tree /></div>)
        }
        
    }
}
GroupUserTree.defaultProps = {
    "visible":false
}
const select = {
    all:false
}

class Tree extends Component{
    constructor(props){
        super(props);
        this.state = {
            "data": [],
            "checked":false
        }
    }
    componentDidMount(){
        
    }
    select = ()=>{
        select.all = !select.all;
        this.setState({
           "checked":select.all 
        })
    }
    render(){
        let btnHtml = this.state.checked ?"取消全部选中":"选中所有人"
        return(<div className="tree">
            <Button className="btn" onClick={this.select} type="primary">{btnHtml}</Button>
            <ul>
                {
                    this.state.data.map((e,i)=>{
                        return <GroupControl key={"root"+i} data={e} />
                    })
                }
                
            </ul>
        </div>)
    }
}

class GroupControl extends Component{
    constructor(props){
        super(props);
    }
    render(){
        let children = typeof (this.props.data.children) == "object" ? this.props.data.children:[];
        let compUserList = typeof (this.props.data.compUserList) == "object" ? this.props.data.compUserList : [];
        if (children.length == 0 && compUserList.length==0){
            return (<span></span>)
        }else{
            return (<li className="parent_li">
                <span>{this.props.data.groupname}</span>
                <li className="parent_li">
                    <ul className="children">
                        {
                            compUserList.map((e, i) => {
                                return <PersonControl key={"user_span_" + i} data={e} />
                            })
                        }
                        {
                            children.map((e, i) => {
                                return <GroupControl key={"parent_li" + i} data={e} />
                            })
                        }
                    </ul>
                </li></li>)
        }
        
    }
}

class PersonControl extends Component {
    render() {
        var className = select.all?"checkedspan":""
        {
            if (this.props.data.name){
                return (
                    <span className={className}>{this.props.data.name}</span>
                )
            }
        }
        
    }
}


export default GroupUserTree;


